import { Component } from 'react'
import { Grid } from 'semantic-ui-react'

import FooterController from './FooterController'
import FrontPage from '../presentationcomponents/FrontPage'
import WhyMeSection from '../presentationcomponents/WhyMeSection'
import SkillSection from '../presentationcomponents/SkillSection'
import ExperienceSection from '../presentationcomponents/ExperienceSection'
import ProjectSection from '../presentationcomponents/ProjectSection'


export default class FrontPageController extends Component {
    constructor(props) {
        super(props);
        this.state = {
            marketplaceImg: 0,
            journeyStepIndex: 0,
        }

        this.changeMarketplaceImage = this.changeMarketplaceImage.bind(this);
        this.updateJourneyStepIndex = this.updateJourneyStepIndex.bind(this);
    }

    changeMarketplaceImage(marketplaceImg) {
        this.setState({marketplaceImg})
    }

    updateJourneyStepIndex(journeyStepIndex) {
        let indexVal = journeyStepIndex
        if(indexVal >= 4) 
            indexVal = 0;
        else if(indexVal <= -1) 
            indexVal = 4;

        this.setState({journeyStepIndex: indexVal})
    }

    render() {
        return (
            <Grid>
                <Grid.Column width={16} className="main-body-wrapper">
                    <FrontPage />
                </Grid.Column>
                <Grid.Column width={16}>
                    <WhyMeSection />
                </Grid.Column>
                <Grid.Column width={16}>
                    <SkillSection />
                </Grid.Column>
                <Grid.Column width={16}>
                    <ExperienceSection {...this.state}
                        changeMarketplaceImage={this.changeMarketplaceImage}
                        updateJourneyStepIndex={this.updateJourneyStepIndex}
                    />
                </Grid.Column>
                <Grid.Column width={16}>
                    <ProjectSection />
                </Grid.Column>
                <Grid.Column width={16}>
                    <FooterController />
                </Grid.Column>
            </Grid>
        )
    }
}