import { Grid } from 'semantic-ui-react'

import HeroSection from './HeroSection'


export default function FrontPage() {
    return (
        <Grid id="hero-section" className="main-body-section-layout">
            <Grid.Column width={2} only="computer" />
            <Grid.Column width={14} only="computer">
                <HeroSection />
            </Grid.Column>

            <Grid.Column width={1} only="tablet" />
            <Grid.Column width={15} only="tablet">
                <HeroSection />
            </Grid.Column>

            <Grid.Column width={16} only="mobile">
                <HeroSection />
            </Grid.Column>

            <Grid.Column width={16} className="add-border" />

        </Grid>
    )
}