import React from 'react';
import { Grid } from 'semantic-ui-react'

import courseCouch from '../../images/course_couch.png'
import clerisySolution from '../../images/clerisy_solution.png'
import refier from '../../images/refier.png'
import journey_img_1 from '../../images/graduation.svg'
import work1 from '../../images/work1.svg'
import work2 from '../../images/work2.svg'
import work3 from '../../images/work3.svg'
import activeJourneyPoint_img from '../../images/active-button.svg'


/* eslint-disable react/prop-types */
export default class ProjectSection extends React.Component {
    constructor(props) {
        super(props);

        this.rolling = null;
        this.startJourneyRoll = this.startJourneyRoll.bind(this);
        this.stopJourneyRoll = this.stopJourneyRoll.bind(this);
        this.REFIER_JOURNEY = {
            0: {
                mainIllustration: journey_img_1,
                illustrationCssClass: 'user-journey-5-img',
                header: 'Graduation',
                explainLine1: 'Degree - B.Tech in Computer Science',
                explainLine2: 'Year of graduation - 2018',
            },
            1: {
                mainIllustration: work1,
                illustrationCssClass: 'user-journey-5-img',
                header: 'Intern at CourseCouch',
                explainLine1: 'Worked with React on their b2c marketplace that connects learners to experts locally',
                explainLine2: 'September 2017 to January 2018',
                workIllustration: courseCouch
            },
            2: {
                mainIllustration: work2,
                illustrationCssClass: 'user-journey-5-img',
                header: 'Intern at Clerisy Solutions',
                explainLine1: 'Services company that I worked with Php, Laravel',
                explainLine2: 'January 2018 to May 2018',
                workIllustration: clerisySolution
            },
            3: {
                mainIllustration: work3,
                illustrationCssClass: 'user-journey-5-img',
                header: 'Fullstack Developer at Refier',
                explainLine1: 'Technology - React, Django, React-Native',
                explainLine2:  'Marketplace to connect Trainers, Mentors, Coaches with Learners',
                workIllustration: refier
            }
        }
    }

    componentDidMount() {
        // if (!isSmallDevice())
        this.startJourneyRoll();
    }

    startJourneyRoll() {
        clearInterval(this.rolling);
        this.rolling = null;

        this.rolling = setInterval(() => {
            this.props.updateJourneyStepIndex(this.props.journeyStepIndex + 1)
        }, 5000)
    }

    stopJourneyRoll() {
        clearInterval(this.rolling)
        this.rolling = null
        this.props.updateJourneyStepIndex(4)
    }

    componentWillUnmount() {
        clearInterval(this.rolling)
        this.rolling = null
    }

    render() {
        return (
            <Grid id="experience-section">
                {/* <div className='footer-mask'>
                    <div className='footer-background'></div>
                </div> */}
                <Grid.Column width={16} style={{textAlign: 'center'}}>
                    <h1 className="header-text">
                        Experience
                    </h1>
                </Grid.Column>

                <Grid.Column width={16} 
                    className="learning-steps" only="computer tablet">
                    <div className="learning-body"> 
                        {/* // onMouseEnter={this.stopJourneyRoll}> */}
                        <div className="learning-left">
                            <div className="experience-explain">
                                <h1 className="experience-explain-number">
                                    {'0' + (this.props.journeyStepIndex + 1)}
                                </h1>
                                <p className="experience-explain-title">
                                    {this.REFIER_JOURNEY[this.props.journeyStepIndex] 
                                        ? this.REFIER_JOURNEY[this.props.journeyStepIndex].header 
                                        : this.REFIER_JOURNEY[0].header
                                    }
                                </p>
                                <p className="experience-explain-text">
                                    {this.REFIER_JOURNEY[this.props.journeyStepIndex] 
                                        ? this.REFIER_JOURNEY[this.props.journeyStepIndex].explainLine1 
                                        : this.REFIER_JOURNEY[0].explainLine1
                                    }
                                </p>
                                <p className="experience-explain-text">
                                    {this.REFIER_JOURNEY[this.props.journeyStepIndex] 
                                        ? this.REFIER_JOURNEY[this.props.journeyStepIndex].explainLine2 
                                        : this.REFIER_JOURNEY[0].explainLine2
                                    }
                                </p>
                            </div>
                            <div className="learning-wheel">
                                <div className="learning-wheel-body"></div>
                                <div className="learning-wheel-body-inner"></div>
    
                                <div className={this.props.journeyStepIndex === 0 
                                    ? 'learning-wheel-inner-point2' 
                                    : 'learning-wheel-inner-point learning-wheel-inner-point2'}>
                                    {this.props.journeyStepIndex === 0
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 1 
                                    ? 'learning-wheel-inner-point3' 
                                    : 'learning-wheel-inner-point learning-wheel-inner-point3'}>
                                    {this.props.journeyStepIndex === 1
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 2 
                                    ? 'learning-wheel-inner-point4' 
                                    : 'learning-wheel-inner-point learning-wheel-inner-point4'}>
                                    {this.props.journeyStepIndex === 2
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 3 
                                    ? 'learning-wheel-inner-point5' 
                                    : 'learning-wheel-inner-point learning-wheel-inner-point5'}>
                                    {this.props.journeyStepIndex === 3
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                            </div>
                        </div>
                        <div className="learning-right">
                            <img className="resume-journey-img"
                                src={this.REFIER_JOURNEY[this.props.journeyStepIndex] 
                                        ? this.REFIER_JOURNEY[this.props.journeyStepIndex].mainIllustration 
                                        : this.REFIER_JOURNEY[0].mainIllustration}
                            />
                            {this.REFIER_JOURNEY[this.props.journeyStepIndex]
                            && this.REFIER_JOURNEY[this.props.journeyStepIndex].workIllustration
                                ? <img className="work-illustraction" 
                                    src={this.REFIER_JOURNEY[this.props.journeyStepIndex].workIllustration} />
                                : null
                            }
                        </div>
                    </div>
                </Grid.Column>

                <Grid.Column width={16} style={{padding: 0}}
                    className="mob-learning-steps" only="mobile">
                    {/* <div className="mob-learning-steps" id="learner-journey-mob"> */}
                        <div className="mob-learning-body">
                            {/* <div className="mob-right-arrow"
                                onClick={() => this.props.updateJourneyStepIndex(this.props.journeyStepIndex - 1)}>
                                <img src={mob_right_arrow} />
                            </div>

                            <div className="mob-left-arrow"
                                onClick={() => this.props.updateJourneyStepIndex(this.props.journeyStepIndex + 1)}>
                                <img src={mob_left_arrow} />
                            </div> */}

                            <div className="mob-learning-wheel">
                                <div className="learning-wheel-body-inner"></div>
                                
                                <div className={this.props.journeyStepIndex === 0 
                                    ? 'learning-wheel-inner-point2' 
                                    : 'learning-wheel-inner-point learning-wheel-inner-point2'}>
                                    {this.props.journeyStepIndex === 0
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 1 
                                    ? 'learning-wheel-inner-point3' 
                                    : 'learning-wheel-inner-point learning-wheel-inner-point3'}>
                                    {this.props.journeyStepIndex === 1
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 2 
                                    ? 'learning-wheel-inner-point4' 
                                    : 'learning-wheel-inner-point learning-wheel-inner-point4'}>
                                    {this.props.journeyStepIndex === 2
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 3 
                                    ? 'learning-wheel-inner-point5' 
                                    : 'learning-wheel-inner-point learning-wheel-inner-point5'}>
                                    {this.props.journeyStepIndex === 3
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
{/* 
                                <div className={this.props.journeyStepIndex === 0 ?
                                    "learning-wheel-inner-point1" :
                                    "learning-wheel-inner-point learning-wheel-inner-point1"}>
                                    {this.props.journeyStepIndex === 0
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 1 ?
                                    "learning-wheel-inner-point2" :
                                    "learning-wheel-inner-point learning-wheel-inner-point2"}>
                                    {this.props.journeyStepIndex === 1
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 2 ?
                                    "learning-wheel-inner-point3" :
                                    "learning-wheel-inner-point learning-wheel-inner-point3"}>
                                    {this.props.journeyStepIndex === 2
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 3 ?
                                    "learning-wheel-inner-point4" :
                                    "learning-wheel-inner-point learning-wheel-inner-point4"}>
                                    {this.props.journeyStepIndex === 3
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 4 ?
                                    "learning-wheel-inner-point5" :
                                    "learning-wheel-inner-point learning-wheel-inner-point5"}>
                                    {this.props.journeyStepIndex === 4
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div>
                                <div className={this.props.journeyStepIndex === 5 ?
                                    "learning-wheel-inner-point6" :
                                    "learning-wheel-inner-point learning-wheel-inner-point6"}>
                                    {this.props.journeyStepIndex === 5
                                        ? <img src={activeJourneyPoint_img} />
                                        : null
                                    }
                                </div> */}
                            </div>

                            <div className="mob-learning-top">
                                <div className="mob-learning-explain">
                                    {/* <h1 className="mob-learning-explain-number">
                                        {"0" + (this.props.journeyStepIndex + 1)}
                                    </h1>
                                    <p className="mob-learning-explain-title">
                                        {this.REFIER_JOURNEY[this.props.journeyStepIndex] ?
                                            this.REFIER_JOURNEY[this.props.journeyStepIndex].header : this.REFIER_JOURNEY[0].header
                                        }
                                    </p> */}
                                    
                                    <div className="mob-experience-components">
                                        <h1 className="mob-learning-explain-number">
                                            {'0' + (this.props.journeyStepIndex + 1)}
                                        </h1>
                                        <p className="mob-learning-explain-title"
                                            style={{marginLeft: '15px'}}>
                                            {this.REFIER_JOURNEY[this.props.journeyStepIndex] 
                                                ? this.REFIER_JOURNEY[this.props.journeyStepIndex].header 
                                                : this.REFIER_JOURNEY[0].header
                                            }
                                        </p>
                                    </div>
                                    <p className="experience-explain-text">
                                        {this.REFIER_JOURNEY[this.props.journeyStepIndex] 
                                            ? this.REFIER_JOURNEY[this.props.journeyStepIndex].explainLine1 
                                            : this.REFIER_JOURNEY[0].explainLine1
                                        }
                                    </p>
                                    <p className="experience-explain-text">
                                        {this.REFIER_JOURNEY[this.props.journeyStepIndex] 
                                            ? this.REFIER_JOURNEY[this.props.journeyStepIndex].explainLine2 
                                            : this.REFIER_JOURNEY[0].explainLine2
                                        }
                                    </p>
                                </div>
                            </div>

                            <div className="mob-learning-right">
                                {/* <img className={this.REFIER_JOURNEY[this.props.journeyStepIndex] ?
                                    this.REFIER_JOURNEY[this.props.journeyStepIndex].illustrationCssClass : this.REFIER_JOURNEY[0].illustrationCssClass}
                                    src={this.REFIER_JOURNEY[this.props.journeyStepIndex] ?
                                        this.REFIER_JOURNEY[this.props.journeyStepIndex].mainIllustration : this.REFIER_JOURNEY[0].mainIllustration}
                                /> */}

                                <img className="resume-journey-img"
                                    src={this.REFIER_JOURNEY[this.props.journeyStepIndex] 
                                            ? this.REFIER_JOURNEY[this.props.journeyStepIndex].mainIllustration 
                                            : this.REFIER_JOURNEY[0].mainIllustration}
                                    />
                                {/* {this.REFIER_JOURNEY[this.props.journeyStepIndex]
                                && this.REFIER_JOURNEY[this.props.journeyStepIndex].workIllustration
                                    ? <img className="work-illustraction" 
                                        src={this.REFIER_JOURNEY[this.props.journeyStepIndex].workIllustration} />
                                    : null
                                } */}
                            </div>
                        {/* </div> */}
                    </div>

                </Grid.Column>


            </Grid>
        )
    }
}
/* eslint-enable react/prop-types */