import { Grid } from 'semantic-ui-react'

// import qwiki from '../../images/project_images/qwiki.png'
import qwiki from '../../images/qwiki.svg'
// import videoStream from '../../images/video-stream.svg'
import videoStreamAlt from '../../images/video-stream-alt.svg'
// import bootstrap from '../../images/bootstrap-carte.svg'
import bootstrapAlt from '../../images/bootstrap_alt.svg'


export default function ProjectSection() {
    return (
        <Grid id="project-section">
            {/* <div className='footer-mask'>
                <div className='footer-background'></div>
            </div> */}

            <Grid.Column width={16} style={{textAlign: 'center'}}>
                <h1 className="header-text" style={{margin: 0}}>
                    Side Projects
                </h1>
            </Grid.Column>
            <Grid.Column width={16} style={{marginTop: '30px'}}>
                <Grid className="project-image-wrapper-container">
                    <Grid.Column width={8} className="image-card-wrapper" only='computer'>
                        <div className="image-card">
                            <img src={qwiki} />
                        </div>
                        <div className="text-card">
                            <span className="normal-text"
                                style={{marginTop: '10px'}}>
                                <a href="https://github.com/checkmate-bitch/QWiki" target="_blank" 
                                    rel="noreferrer" style={{color: '#fbfbfb'}}>
                                    <b>QWiki</b> - A chrome extention that allows you to search for any word or a string of words on your current page from Wikipedia without leaving your page. (Click Here)
                                </a>
                            </span>
                        </div>
                    </Grid.Column>
                    <Grid.Column width={4} className="image-card-wrapper" only='computer'>
                        <div className="image-card">
                            <img src={bootstrapAlt} />
                        </div>
                        <div className="text-card">
                            <span className="normal-text" style={{marginTop: '10px'}}>
                                <b>Bootstrap-a-la-carte</b> - Build customized bootstrap components for your website instantly.
                            </span>
                        </div>
                    </Grid.Column>
                    <Grid.Column width={4} className="image-card-wrapper" only='computer'>
                        <div className="image-card">
                            <img src={videoStreamAlt} />
                        </div>
                        <div className="text-card">
                            <span className="normal-text"
                                style={{marginTop: '10px'}}>
                                <b>Rtc Streamer</b> - A webapp that allows live streaming and live chat. Built on top of WebRTC.
                            </span>
                        </div>
                    </Grid.Column>

                    <Grid.Column width={16} className="image-card-wrapper" only='tablet'>
                        <div className="image-card">
                            <img src={qwiki} />
                        </div>
                        <div className="text-card">
                            <span className="normal-text"
                                style={{marginTop: '10px'}}>
                                <a href="https://github.com/checkmate-bitch/QWiki" target="_blank" 
                                    rel="noreferrer" style={{color: '#fbfbfb'}}>
                                    <b>QWiki</b> - A chrome extention that allows you to search for any word or a string of words on your current page from Wikipedia without leaving your page. (Click Here)
                                </a>
                            </span>
                        </div>
                    </Grid.Column>
                    <Grid.Column width={8} className="image-card-wrapper" only='tablet'>
                        <div className="image-card">
                            <img src={bootstrapAlt} />
                        </div>
                        <div className="text-card">
                            <span className="normal-text" style={{marginTop: '10px'}}>
                                <b>Bootstrap-a-la-carte</b> - Build and copy customized bootstrap components for your website instantly.
                            </span>
                        </div>
                    </Grid.Column>
                    <Grid.Column width={8} className="image-card-wrapper" only='tablet'>
                        <div className="image-card">
                            <img src={videoStreamAlt} />
                        </div>
                        <div className="text-card">
                            <span className="normal-text"
                                style={{marginTop: '10px'}}>
                                <b>Rtc Streamer</b> - A webapp that allows live streaming and instant communication through chat. Built on top of WebRTC.
                            </span>
                        </div>
                    </Grid.Column>

                    <Grid.Column width={16} className="image-card-wrapper" only='mobile'>
                        <div className="image-card">
                            <img src={qwiki} />
                        </div>
                        <div className="text-card">
                            <span className="normal-text"
                                style={{marginTop: '10px'}}>
                                <a href="https://github.com/checkmate-bitch/QWiki" target="_blank" 
                                    rel="noreferrer" style={{color: '#fbfbfb'}}>
                                    <b>QWiki</b> - A chrome extention that allows you to search for any word or a string of words on your current page from Wikipedia without leaving your page. (Click Here)
                                </a>
                            </span>
                        </div>
                    </Grid.Column>
                    <Grid.Column width={16} className="image-card-wrapper" only='mobile'>
                        <div className="image-card">
                            <img src={bootstrapAlt} />
                        </div>
                        <div className="text-card">
                            <span className="normal-text" style={{marginTop: '10px'}}>
                                <b>Bootstrap-a-la-carte</b> - Build and copy customized bootstrap components for your website instantly.
                            </span>
                        </div>
                    </Grid.Column>
                    <Grid.Column width={16} className="image-card-wrapper" only='mobile'>
                        <div className="image-card">
                            <img src={videoStreamAlt} />
                        </div>
                        <div className="text-card">
                            <span className="normal-text"
                                style={{marginTop: '10px'}}>
                                <b>Rtc Streamer</b> - A webapp that allows live streaming and instant communication through chat. Built on top of WebRTC.
                            </span>
                        </div>
                    </Grid.Column>
                </Grid>
            </Grid.Column>
        </Grid>
    )
}